<template>
    <div id="app">
        <video-player
            class="video-player-box"
            ref="videoPlayer"
            :options="playerOptions"
            :playsinline="true"
            customEventName="customstatechangedeventname"
            @play="onPlayerPlay($event)"
            @pause="onPlayerPause($event)"
            @ended="onPlayerEnded($event)"
            @waiting="onPlayerWaiting($event)"
            @playing="onPlayerPlaying($event)"
            @loadeddata="onPlayerLoadeddata($event)"
            @timeupdate="onPlayerTimeupdate($event)"
            @canplay="onPlayerCanplay($event)"
            @canplaythrough="onPlayerCanplaythrough($event)"
            @statechanged="playerStateChanged($event)"
            @ready="playerReadied"
        >
        </video-player>
    </div>
</template>
<script>
export default {
    data() {
        return {
            playerOptions: {
                // videojs options
                muted: true,
                autoplay: true,
                playbackRates: [0.7, 1.0, 1.5, 2.0],
                sources: [
                    {
                        type: "video/mp4",
                        src:
                            "https://ppmiao0628.github.io/ppm.github.io/video/%E9%A6%96%E5%B0%94%E5%A1%942.mp4",
                    },
                ],
                loop: false, // 是否视频一结束就重新开始。
                preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
                language: "zh-CN",
                // aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                poster: "", // 封面地址
                notSupportedMessage: "此视频暂无法播放，请稍后再试", // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
                controlBar: {
                    timeDivider: false, // 当前时间和持续时间的分隔符
                    durationDisplay: false, // 显示持续时间
                    remainingTimeDisplay: false, // 是否显示剩余时间功能
                    fullscreenToggle: false, // 是否显示全屏按钮
                },
            },
            videoUrl:
                "https://ppmiao0628.github.io/ppm.github.io/video/%E9%A6%96%E5%B0%94%E5%A1%942.mp4",
        };
    },
    mounted() {
        console.log("this is current player instance object", this.player);
    },
    computed: {
        player() {
            return this.$refs.videoPlayer.player;
        },
    },
    methods: {
        // listen event
        onPlayerPlay(player) {
            console.log("player play!", player);
        },
        onPlayerPause(player) {
            console.log("player pause!", player);
        },
        onPlayerEnded(player) {
            console.log("player onPlayerEnded!", player);
        },
        onPlayerWaiting(player) {
            console.log("player onPlayerWaiting!", player);
        },
        onPlayerPlaying(player) {
            console.log("player onPlayerPlaying!", player);
        },
        onPlayerLoadeddata(player) {
            console.log("player onPlayerLoadeddata!", player);
        },
        onPlayerTimeupdate(player) {
            console.log("player onPlayerTimeupdate!", player);
        },
        onPlayerCanplay(player) {
            console.log("player onPlayerCanplay!", player);
            this.player.play();
        },
        onPlayerCanplaythrough(player) {
            console.log("player onPlayerCanplaythrough!", player);
        },
        // ...player event

        // or listen state event
        playerStateChanged(playerCurrentState) {
            console.log("player current update state", playerCurrentState);
        },

        // player is ready
        playerReadied(player) {
            console.log("the player is readied", player);
            // you can use it to do something...
            // player.[methods]
        },
    },
};
</script>
